import React from 'react';
import { Row, Col, Tabs,Select,Form,Input,DatePicker,Button } from 'antd';
import {RequireUtils} from 'utils';
import moment from 'moment';
import mystyle from './tabstyle.less' ;
const createForm = Form.create;
const FormItem = Form.Item;
const TabPane = Tabs.TabPane;
const Option = Select.Option;

function noop() {
  return false;
}


class PlanListUpdate extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      loading:false,
      updateData:{},
      contractoption:[],
      $addroption:[],
      companyoption:[],
      productoption:[],
      form:{
        plan_status:"",
        id:"",
        deliver_id:"",//
        buyer_id:"", //
        contract_id:"", //
        recv_id:"",
        plan_weight:"",
        start_date:"",
        end_date:"",
        plan_interval:"",
        price:"",
        prod_id:""
      }
    };
    this.callback = this.callback.bind(this);
  }
  callback(key) {
    console.log(key);
  }

  componentDidMount() {
    const {updateData} = this.props ;
    this.setState({
      form:updateData
    })
  }

  render() {
    const {updateData} = this.props ;
    const {
      plan_status,
      id,
      deliver_id,
      saler_name,
      contract_id,
      addr_name,
      plan_weight,
      start_date,
      end_date,
      price,
      prod_name,
      contract_name,
      plan_interval} = updateData;
    /**
     * 
     * @type {{plan_status, id: *, deliver_id: *, buyer_id: *, contract_id: *, recv_id: *, plan_weight: *, bill_name: *, start_date: *, end_date: *, plan_interval: *}}
     */
    const formItemLayout = {
      labelCol: { span: 6 },
      wrapperCol: { span: 18 },
    };

    const { getFieldDecorator } = this.props.form;

    return (
      <div className={'my-custom-padding'}>
        <main>
        <Form >
        <Row gutter={8} >
          <Col span={11}>
            <FormItem
              {...formItemLayout}
              label="">
              {getFieldDecorator('saler_name', {
              })(
               <span>{saler_name}</span>
              )}
            </FormItem>
          </Col>
          <Col span={2}></Col>
          <Col span={11}>
            <FormItem
              {...formItemLayout}
              label="">
              {getFieldDecorator('addr_name', {
              })(
                <span>{addr_name}</span>
              )}
            </FormItem>
          </Col>
        </Row>
        <Row gutter={8} style={{marginTop:'10px'}}>
          <Col span={11}>
            <FormItem
              {...formItemLayout}
              label="">

              {getFieldDecorator('plan_status', {
                initialValue:plan_status+"",
                rules: [{ required: true, message: '' }]
              })(
                <span>
                  {plan_status==0?'':''}
                </span>
              )}
            </FormItem>
          </Col>
          <Col span={2}></Col>
          <Col span={11}>
            <FormItem
              {...formItemLayout}
              label="">
              {getFieldDecorator('deliver_id', {
              })(
              <span>
                {deliver_id==0?'':''}
              </span>
              )}

            </FormItem>
          </Col>
        </Row>
        <Row gutter={8} style={{marginTop:'10px'}}>
          <Col span={11}>
            <FormItem
              {...formItemLayout}
              label="">
              {getFieldDecorator('plan_weight', {

              })(
                <span>
                  {plan_weight}
                </span>
              )}

            </FormItem>
          </Col>
          <Col span={2}></Col>
          <Col span={11}>
            <FormItem
              {...formItemLayout}
              label="">
              {getFieldDecorator('plan_interval', {
              })(
                  <span>{plan_interval}</span>
              )}

            </FormItem>
          </Col>

        </Row>
          <Row gutter={8} style={{marginTop:'10px'}}>
            <Col span={11}>
              <FormItem
                {...formItemLayout}
                label="">
                {getFieldDecorator('prod_name', {
                })(
                 <span>
                   {prod_name}
                 </span>
                )}
              </FormItem>
            </Col>
            <Col span={2}></Col>
            <Col span={11}>
              <FormItem
                {...formItemLayout}
                label="">
                {getFieldDecorator('contract_name', {

                })(
                  <span>{contract_name}</span>
                )}
              </FormItem>
            </Col>
          </Row>
        <Row gutter={8} style={{marginTop:'10px'}}>
          <Col span={3}>
            
          </Col>
          <Col span={21}>
            {start_date}{end_date}
          </Col>
        </Row>
        </Form>
        </main>
      </div >
    )
  }
}
PlanListUpdate = createForm()(PlanListUpdate);
export default PlanListUpdate;



